/* Yiayia's scss
Written by Dylan Araps */
/* Variables */
/* Reset Styles */
html,
body,
ul,
h1,
h2 {
  margin: 0;
  padding: 0;
}

body {
  background: #FFFFFF;
}

/* Box Sizing */
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* Yiayia's Font */
@font-face {
  font-family: 'yiayias';
  src: url("../fonts/yiayias.woff2") format("woff2"), url("../fonts/yiayias.woff") format("woff");
}
/* Nav Styles */
nav {
  height: 50px;
  margin: 0 5%;
  position: fixed;
  top: 0;
  width: 90%;
}
nav a {
  color: #FFFFFF;
  text-decoration: none;
}
nav li:first-child {
  float: left;
  font: 1.5em 'yiayias';
  line-height: 50px;
  margin-left: 0;
}
nav li:first-child:hover {
  border: 0;
}
nav li {
  display: inline-block;
  float: right;
  font: 1em Segoe UI,Segoe, Tahoma, Geneva, sans-serif;
  line-height: 50px;
  margin-left: 30px;
  transition: .2s;
}
nav li:hover {
  border-bottom: 2px solid #FFFFFF;
  opacity: .7;
}

/* Text Styles */
h2,
p {
  font-family: Segou UI, Segoe, Tahoma, Geneva, sans-serif;
}

h2 {
  color: #111111;
  font-size: 3em;
  font-weight: bold;
}

p {
  font-size: 1em;
}

/* Section Styles */
/* Section */
/* Section 1 */
.section1 {
  align-items: center;
  background-size: cover;
  background: url("../img/bg/section1alt2.jpg") no-repeat 50% 50%;
  display: flex;
  flex-direction: column;
  height: 70vh;
  justify-content: center;
  overflow: hidden;
}
.section1 h1,
.section1 h2 {
  color: #FFFFFF;
}
.section1 h1 {
  font: 8em 'yiayias';
}
.section1 h2 {
  font-size: 2.5em;
  font-weight: normal;
}

.section2 {
  height: 50vh;
}

@media only screen and (max-width: 500px) {
  nav li {
    margin: 0 10px 0 10px;
  }
  nav li:first-child {
    display: none;
  }

  .section1 h1 {
    font-size: 6em;
  }
}


